#pinContainer {
    height: 100vh;
    width: 100vw;
    overflow: hidden;
}
#pinContainer .panel {
    height: 100vh;
    width: 100vw;
    position: absolute;
    text-align: center;
}
.panel span {
    position: relative;
    display: block;
    top: 50%;
    font-size: 80px;
}

.panel.blue {
    visibility: hidden;
    background-color: #3883d8;
}

.panel.about {
    background-color: #404040;
}

.panel.green {
    background-color: #22d659;
}

.panel.faculty {
    background: linear-gradient(0deg, #6441a5 0%, #2a0845 100%);
}
.panel.bordeaux {
    background-color: #953543;
}

/* faculty message */

.card {
    opacity: 0.7;
}

.prof-avatar {
    width: 20%;
    position: relative;
    margin-left: 40%;
}

.faculty-message {
    text-align: center;
    margin-top: 15%;
    font-family: "Poppins", sans-serif;
    font-weight: 300;
}
.faculty-h1 {
    margin-bottom: -10%;
    color: white;
}
.card-text {
    padding-top: 2%;
}
.card {
    width: 10rem;
}
.faculty-name,
.faculty-designation {
    font-size: 1rem;
}
