/* Menu items hover effect */
.MenuItems {
    font-family: "Raleway", Arial, sans-serif;
    text-align: center;
    text-transform: uppercase;
    font-weight: 500;
}
.MenuItems * {
    box-sizing: border-box;
    color: #f9c75e !important;
}
.MenuItems span {
    display: inline-block;
    list-style: outside none none;
    margin: 0 1.5em;
    padding: 0;
}
.MenuItems span {
    padding: 0.5em 0;
    color: rgba(255, 255, 255, 0.5);
    position: relative;
    letter-spacing: 1px;
    text-decoration: none;
}
.MenuItems span:hover,
MenuItems span:active {
    text-decoration: none;
}

.MenuItems span:before,
.MenuItems span:after {
    position: absolute;
    transition: all 0.35s ease;
}
.MenuItems span:before {
    bottom: 0;
    display: block;
    height: 3px;
    width: 0%;
    content: "";
    background-color: #2980b9;
}

.MenuItems span:after {
    left: 0;
    top: 0;
    padding: 0.5em 0;
    position: absolute;
    content: attr(data-hover);
    color: #ffffff;
    white-space: nowrap;
    max-width: 0%;
    overflow: hidden;
}
.MenuItems span:hover:before,
.MenuItems .current a:before {
    opacity: 1;
    width: 100%;
}
.MenuItems span:hover:after,
.MenuItems .current span:after {
    max-width: 100%;
}

.current {
    color: #ffffff !important;
}

