.Section-Wipes-Container {
    z-index: 10 !important;
}

/* Logo */

.Home-Logo {
    transform-style: preserve-3d;
    width: 50%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0%;
    transition: all 0.1s ease;
    padding-top: 16%;
}

.Logo-Perspective {
    perspective: 700px;
}

body {
    background: rgb(0, 0, 0);
    background: linear-gradient(90deg, #0f0c29 0%, #302b63 50%, #24243e 100%);
    overflow-x: hidden;
}

.History {
    margin-top: 1000px;
}

.container {
    height: 800px;
    z-index: 2;
}

.Particles {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0%;
    z-index: 2;
}
